.menu {
  &-links {
    margin-top: 5px;
    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }
  &-brand {
    text-align: center;
    margin: 0 auto;
    padding: 15px 15px 10px;
    &__anchor {
      display: block;
    }
    &__image {
      width: 50%;
    }
  }
  &-list {
    &__item  {
      border-bottom: 1px solid var(--dark-blue);
      color: #fff;
      font-size: $font-x-small;
      padding: 5px 15px;
      &:after {
        display: inline-block;
        float: right;
        margin-top: 3%;
        @media only screen and (min-width: 320px) {
          content: ">";
        }
        @media only screen and (min-width: 490px) {
          content: "❯";
        }
      }
      &:last-child {
        border-bottom: none;
      }
    }
    &__anchor {
      color: #fff;
      display: inline-block;
      font-size: $font-medium;
      font-weight: bold;
      text-transform: capitalize;
      text-decoration: underline;
    }
    &__description {
      color: #fff;
      display: block;
      text-transform: initial;
      font-weight: normal;
      font-size: $font-x-small;
      padding: 0 15px 0 0;
    }
  }
}
